/* ===========================



 =========  3.1 Menu Style ======== 



 =========================== */



.header .navbar {
	position: absolute;
    left: 0;
    right: 0;
    transition-delay: 0s;
    transition-duration: 0.6s;
    transition-property: all;
    transition-timing-function: ease;
    /*background: rgba(255, 255, 255, 0.9);*/
    padding: 18px 0;
}
.header .navbar.stuck {
	position: fixed;
	top: 0;
	width: 100%;
	left: 0;
	right: 0;
	/*padding-top: 0;*/
	background-color: #FFF;
	min-height: 79px;
	border-bottom: 3px double #882575;
}
.header .box-logo {
	float: left;
    margin-top: 0;
    transition-delay: 0s;
    transition-duration: 0.6s;
    transition-property: all;
    transition-timing-function: ease;
    width: 175px;
    position: relative;
    z-index: 1;
}
@media(min-width: 768px){
	.header .box-logo {
		width: 23%;
	}
}
.header .box-logo img {
	transition-delay: 0s;
	transition-duration: 0.6s;
	transition-property: all;
	transition-timing-function: ease;
}
.header .box-logo .logo_hove {
	display: none
}
.header .navbar.stuck .box-logo .logo {
	display: none
}
.header .navbar.stuck .box-logo {
	margin-top: 0
}
.header .navbar.stuck .box-logo .logo_hove {
	display: block
}
.header nav {
	float: right;
	position: relative;
	width: 100%;
}
@media(min-width: 768px){
	.header nav {
		width: 75%;
		padding-right: 0;
	}
}
.header nav #menu-button {
	width: 28px;
	font-size: 0;
	float: right;
	height: 18px;
	position: relative;
	margin-top: 11px;
	-webkit-transform: rotate(0deg);
	-moz-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
	 -webkit-transition: .5s ease-in-out;
	 -moz-transition: .5s ease-in-out;
	 -o-transition: .5s ease-in-out;
	 transition: .5s ease-in-out;
	cursor: pointer;
	padding-right: 0;
}

@media(min-width: 768px){
	.header nav #menu-button {
		margin-top: 11px;
	}
}
@media(min-width: 992px){
	.header nav #menu-button {
		margin-top: 16px;
	}
}
@media(min-width: 1200px){
	.header nav #menu-button {
		margin-top: 21px;
	}
}


.header .navbar.stuck nav #menu-button {
	margin-top: 11px;
}
@media(min-width: 992px){
	.header .navbar.stuck nav #menu-button{
		margin-top: 16px;
	}
}	
@media(min-width: 1200px){
	.header .navbar.stuck nav #menu-button{
		margin-top: 21px;
	}
}

.header nav #menu-button span {
	display: block;
	position: absolute;
	z-index: 60;
	height: 3px;
	width: 100%;
	background: #fff;
	opacity: 1;
	left: 0;
	-webkit-transform: rotate(0deg);
	-moz-transform: rotate(0deg);
	-o-transform: rotate(0deg);
	transform: rotate(0deg);
 -webkit-transition: .25s ease-in-out;
 -moz-transition: .25s ease-in-out;
 -o-transition: .25s ease-in-out;
 transition: .25s ease-in-out;
}
.header .navbar.stuck nav #menu-button span {
	background: #882575;
}
 .header nav #menu-button span:nth-child(1) {
 top: 0px;
}
 .header nav #menu-button span:nth-child(2) {
 top: 7px;
}
 .header nav #menu-button span:nth-child(3) {
 top: 14px;
}
 .header nav #menu-button.open span:nth-child(1) {
 top: 10px;
 -webkit-transform: rotate(135deg);
 -moz-transform: rotate(135deg);
 -o-transform: rotate(135deg);
 transform: rotate(135deg);
}
 .header nav #menu-button.open span:nth-child(2) {
 opacity: 0;
 left: -60px;
}
 .header nav #menu-button.open span:nth-child(3) {
 top: 10px;
 -webkit-transform: rotate(-135deg);
 -moz-transform: rotate(-135deg);
 -o-transform: rotate(-135deg);
 transform: rotate(-135deg);
}
.header nav ul {
	display: none;
	position: absolute;
	right: 8%;
	top: 0;
	transition-delay: 0s;
	transition-duration: 0.6s;
	transition-property: all;
	transition-timing-function: ease;
}
.header .navbar.stuck nav ul {
	margin-top: 0;
}
.header nav ul > li {
	display: inline-block;
	text-transform: uppercase;
	font-size: 15px;
	padding-left: 35px;
	font-weight: 600;
	letter-spacing: 2px;
	position: relative
}
.header nav ul > li > a {
	color: #fff;
	line-height: 67px;
	display: block;
	letter-spacing: 1px;
	display: block;
}
@media(min-width: 768px){
	.header nav ul > li > a{
		line-height: 42px;
	}
}
@media(min-width: 992px){
	.header nav ul > li > a{
		line-height: 56px;
	}
}
@media(min-width: 1200px){
	.header nav ul > li > a{
		line-height: 67px;
		letter-spacing: 2px;
	}
}

.header .navbar.stuck nav ul > li > a {
	color: #882575;
}

.header nav ul > li:hover ul {
	display: block
}

.header nav ul > li ul {
	position: absolute;
	right: 0;
	top: 45px;
	border: 2px solid #4d4d4d;
	width: 203px;
	padding-left: 24px;
	padding-right: 26px;
	padding-top: 26px;
	padding-bottom: 6px;
	display: none
}
.header .navbar.stuck nav ul li ul {
	border-color: #FFF;
	top: 77px
}
.header .navbar.stuck nav ul li ul li a {
	color: #FFF
}
.header nav ul > li ul li {
	padding-left: 0;
	font-size: 13px;
}
.header nav ul > li ul li a {
	line-height: 14px;
	padding-bottom: 24px
}
.header .angle-down {
	position: absolute;
	background-image: url(../assets/img/down_arrow.png);
	background-repeat: no-repeat;
	background-position: 0 0;
	width: 13px;
	height: 103px;
	left: 50%;
	bottom: 50px;
	display: inline-block;
	margin-left: -12px;
	z-index: 999;
	-webkit-animation: bounce 2000ms infinite;
	-moz-animation: bounce 2000ms infinite;
	-o-animation: bounce 2000ms infinite;
	-ms-animation: bounce 2000ms infinite;
	animation: bounce 2000ms infinite;
}
